.result {
  height: 100vh;
  display: grid;
  place-items: center;
}
.result_box {
  padding: 10%;
  text-align: center;
  background-color: #64e10e;
  border-radius: 10px;
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.12), 0 2px 3px rgba(0, 0, 0, 0.24);
  padding-top: 0;
  padding-bottom: 50px;
  margin: 20px;
}
